.my-projects-container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    margin: 0;
    background: linear-gradient(#375387 25%, #2e4877 50%, #243a63 75%, #22375f 100%);
}

.my-projects-title {
    background-color: transparent;
    font-size: 38px;
    color: white;
    font-family: 'Montserrat', sans-serif;
}

.my-projects-list {
    position: relative;
    padding: 60px 0;
    top: 0;
    left: 0;
    width: 100%;
    min-width: 190px;
    font-family: 'Montserrat', sans-serif;
}

@media screen and (max-width: 1100px) {
    .my-projects-title {
        font-size: 34px;
    }

    .my-projects-list {
        padding: 50px 0;
    }
}

@media screen and (max-width: 820px) {
    .my-projects-title {
        font-size: 30px;
    }

    .my-projects-list {
        padding: 40px 0;
    }
}

@media screen and (max-width: 660px) {
    .my-projects-title {
        font-size: 26px;
    }

    .my-projects-list {
        padding: 45px 0;
    }

    .my-projects-list {
        padding: 35px 0;
    }
}

@media screen and (max-width: 535px) {
    .my-projects-title {
        font-size: 24px;
    }

    .my-projects-list {
        padding: 30px 0;
    }
}

@media screen and (max-width: 440px) {
    .my-projects-title {
        font-size: 22px;
    }

    .my-projects-list {
        padding: 25px 0;
    }
}

@media screen and (max-width: 360px) {
    .my-projects-title {
        font-size: 20px;
    }

    .my-projects-list {
        padding: 20px 0;
    }
}

@media screen and (max-width: 300px) {
    .my-projects-title {
        font-size: 18px;
    }

    .my-projects-list {
        padding: 15px 0;
    }
}

@media screen and (max-width: 200px) {
    .my-projects-title {
        font-size: 16px;
    }

    .my-projects-list {
        padding: 10px 0;
    }
}