.main-title-container {
    width: 40%;
    max-width: 900px;
    height: 100%;
    position: relative;
    display: inline-block;
    top: 0;
    left: 3%;
    font-family: 'Montserrat', sans-serif;
    background-color: transparent;
    vertical-align: middle;
    z-index: 99;
    color: white;
  }
  
  .title-container {
    position: relative;
    display: block;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    text-align: center;
    margin: 0;
    opacity: 0;
  }

  .show-main-title {
    animation: show-title 1s 2500ms cubic-bezier(0, -0.36, 0.4, 0.99) both 1;
  }

  @keyframes show-title {
    0% {opacity: 0;}
    100% {opacity: 1;}
  }
  
  #main-title {
    position: relative;
    margin: 0;
    padding: 0;
    font-size: 54px;
    top: 0;
    left: 0;
    padding: 0 25px;
  }
  
  #main-description {
    position: relative;
    margin: 10px 0 0 0;
    padding: 0 12px;
    top: 0;
    left: 0;
    font-size: 20px;
  }

  @media screen and (max-width: 1650px) {
    #main-title {
      font-size: 50px;
    }
  
    #main-description {
      font-size: 18px;
    }
  }

    @media screen and (max-width: 1280px) {
    #main-title {
      font-size: 42px;
    }
  
    #main-description {
      font-size: 18px;
    }
  }
  
  @media screen and (max-width: 900px) {
    #main-title {
      font-size: 36px;
    }
  
    #main-description {
      font-size: 16px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .main-title-container {
        width: 100%;
        left: 0;
    }
  }

  @media screen and (max-width: 445px) {
    #main-title {
      font-size: 32px;
    }

    #main-description {
      font-size: 14px;
    }
  }

  @media screen and (max-width: 220px) {
    #main-title {
      font-size: 24px;
    }

    #main-description {
      font-size: 12px;
    }
  }