.loading-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #11264d;
    z-index: 9999;
    transform: translateX(0);
  }

  .preloader {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .preloader svg {
    max-width: 200px;
    border-radius: 3px;
    background: transparent;
    fill: none;
    stroke: #11264d;
    stroke-linecap: round;
    stroke-width: 7%;
}

.hide {
  animation: remove-loading-container 1000ms 1000ms cubic-bezier(1, 0, 0.39, 1.54) both 1;
}

@keyframes remove-loading-container {
  0% {transform: translateX(0);}
  100% {transform: translateX(-105%);}
}

.preloader use {
    stroke: #000000;
    animation: preloader 1300ms linear infinite;
}

@keyframes preloader {
    to { 
        stroke-dashoffset: 0px;
    } 
}

@media screen and (max-width: 1280px), screen and (max-height: 720px) {
  .preloader svg {
    max-width: 160px;
  }
}

@media screen and (max-width: 768px), screen and (max-height: 300px) {
  .preloader svg {
    max-width: 125px;
  }
}

@media screen and (max-width: 445px) {
  .preloader svg {
    max-width: 90px;
  }
}