nav {
    position: fixed;
    top: 20px;
    left: 75%;
    transform: translateX(-75%);
    z-index: 999;
    font-family: 'Montserrat', sans-serif;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  li {
    display: inline;
    padding: 0;
    cursor: pointer;
  }

  .navlink {
    text-decoration: none;
    color: #9196cc;
    transition: color 150ms ease-out, font-size 150ms ease-in;
    font-size: 24px;
    margin: 0 8px;
    opacity: 0;
  }

  .navlink:hover {
    color: #151522;
    font-size: 28px;
  }

  .show-text {
    animation: show-text 500ms 2200ms ease both 1;
  }

  @keyframes show-text {
    0% {opacity: 0;}
    100% {opacity: 1;}
  }

    @media screen and (max-width: 1050px) {
      .navlink {
        font-size: 20px;
      }

      .navlink:hover {
        font-size: 24px;
      }
  }
  
  /* @media screen and (max-width: 900px) {
    .navlink {
      font-size: 16px;
    }

    .navlink:hover {
      font-size: 18px;
    }
  } */
  
  @media screen and (max-width: 768px) {
    nav {
      left: 50%;
      transform: translateX(-50%);
      top: 15px;
    }
  
    .navlink {
      font-size: 16px;
      margin: 0 5px;
    }

    .navlink:hover {
      font-size: 18px;
    }
  }

  @media screen and (max-width: 350px) {
    .navlink {
      font-size: 14px;
    }

    .navlink:hover {
      font-size: 16px;
    }

    nav {
        top: 10px;
    }
  }

  @media screen and (max-width: 270px) {
    .navlink {
      font-size: 12px;
      margin: 0 3px;
    }

    .navlink:hover {
      font-size: 14px;
    }

    nav {
        top: 8px;
    }
  }

  @media screen and (max-width: 220px) {
    .navlink {
      font-size: 10px;
      margin: 0 2px;
    }

    .navlink:hover {
      font-size: 12px;
    }

    nav {
        top: 6px;
    }
  }