.footer {
    position: relative;
    top: 0;
    left: 0;
    width: 100%; 
    margin: 0;
    padding: 0;
    padding: 50px 0 18px 0;
    background: linear-gradient(#22375f 50%, #1c2f52 100%);
}

.contacts-content-container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.contacts-content-child-container {
    position: relative;
    top: 0;
    left: 0;
    margin: 0 75px;
    transition: transform 250ms ease-in-out;
}

.contacts-content-child-container:hover {
    transform: scale(1.1);
}

.contacts-icon {
    width: 60px;
    height: 60px;
    transition: transform 500ms ease;
}

.contacts-icon:hover {
    transform: rotate(360deg);
}

.contacts-value {
    font-size: 15px;
    color: white;
    font-family: 'Montserrat', sans-serif;
    margin-top: 2px;
}

.more-projects-container {
    margin-top: 20px;
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    color: white;
}

.more-projects-link {
    color: #7a7c8b;
    padding: 0 5px;
    text-decoration: none;
    transition: color 150ms ease-in-out;
}

.more-projects-link:hover {
    color: #000000;
}

.fotter-bottom-text {
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    margin-top: 10px;
}

#smiley {
    display: inline-block;
    animation: rotation 1500ms cubic-bezier(0.880, -0.660, 0.265, 1.420) infinite;
}

@keyframes rotation {
    0% { transform: rotate(0deg); }   
    100% { transform: rotate(360deg); }
}

@media screen and (max-width: 768px) {
    .contacts-icon {
        width: 50px;
        height: 50px;
    }

    .contacts-value {
        font-size: 14px;
    }

    .contacts-content-child-container {
        margin: 0 65px;
    }

    .more-projects-container {
        font-size: 14px;
    }

    .fotter-bottom-text {
        font-size: 12px;
    }

    .footer {
        padding: 40px 0 15px 0;
    }
}

@media screen and (max-width: 535px) {
    .contacts-icon {
        width: 40px;
        height: 40px;
    }

    .contacts-value {
        font-size: 12px;
    }

    .contacts-content-child-container {
        margin: 0 50px;
    }

    .more-projects-container {
        font-size: 13px;
    }

    .fotter-bottom-text {
        font-size: 11px;
    }

    .footer {
        padding: 30px 0 12px 0;
    }
}

@media screen and (max-width: 440px) {
    .contacts-content-container {
        flex-direction: column;
    }

    .contacts-content-child-container {
        margin: 20px 0;
    }

    .more-projects-container {
        font-size: 12px;
    }

    .fotter-bottom-text {
        font-size: 10px;
    }

    .footer {
        padding: 25px 0 10px 0;
    }
}

@media screen and (max-width: 260px) {
    .contacts-content-child-container {
        margin: 10px 0;
    }

    .more-projects-container {
        font-size: 10px;
    }

    .fotter-bottom-text {
        font-size: 8px;
    }

    .footer {
        padding: 20px 0 8px 0;
    }
}

@media screen and (max-width: 200px) {
    .contacts-content-child-container {
        margin: 7px 0;
    }

    .contacts-icon {
        width: 30px;
        height: 30px;
    }

    .contacts-value {
        font-size: 10px;
    }

    .more-projects-container {
        font-size: 9px;
    }

    .fotter-bottom-text {
        font-size: 7px;
    }

    .footer {
        padding: 12px 0 6px 0;
    }
}