.my-projects-list-item {
    position: relative;
    display: block;
    top: 0;
    left: 0;
    margin: 0 auto 90px;
    width: 750px;
    height: 400px;
    overflow: hidden;
    cursor: initial;
    box-shadow: 0 20px 80px 0 #000000;
    border: 4px solid #423270;
    border-radius: 8px;
}

/* #506699 */

.left-translation {
    transform: translateX(-12%);
}

.right-translation {
    transform: translateX(12%);
}

.my-projects-image {
    position: relative;
    width: 100%;
    height: 400%;
    top: 0;
    left: 0;
    transform: scale(1);
    transition: transform 500ms cubic-bezier(.075,.82,.165,1), filter 500ms cubic-bezier(.075,.82,.165,1);
}

.project-description-main-container {
    position: absolute;
    top: 0;
    left: -2%;
    width: 104%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transform: scaleX(0);
    transform-origin: left;
    background: linear-gradient(90deg, #141414  0%, #0a0a0a 35%, #000000 100%);
    color: white;
    transition: transform 500ms cubic-bezier(.175,.885,.32,1.275);
    opacity: .8;
    overflow: hidden;
}

.my-project-name {
    position: absolute;
    top: 5px;
    left: 0;
    color: #ffffff;
    width: 100%;
    text-align: center;
    font-size: 28px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;
}

.project-description-container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.my-project-description {
    position: relative;
    top: 0;
    font-size: 18px;
    padding: 0 15px;
    font-weight: 400;
    font-family: 'Montserrat', sans-serif;
}

.my-project-open-btn {
    position: relative;
    margin-top: 15px;
    top: 0;
    left: 0;
    padding: 6px 24px;
    border-radius: 5px;
    border: none;
    background: linear-gradient(90deg, #506699 0%, #3e5896 35%, #304a88 100%);
    color: white;
    font-size: 14px;
    font-family: 'Montserrat', sans-serif;
    transition: background-position 500ms ease;
}

.my-project-open-btn:hover {
    background-position: 180px;
}

.my-projects-list-img-container:hover .my-projects-image {
    transform: scale(1.2);
    transform-origin: center;
    filter: brightness(0.75) blur(2px);
}

.my-projects-list-item:hover .project-description-main-container {
    transform: scaleX(1);
}

