.my-skills-main-container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    font-family: 'Montserrat', sans-serif;
    color: white;
    margin: 0 auto;
}

#c2 {
    position: relative;
    top: 0;
    left: 0;
}

#my-skills-title {
    font-size: 32px;
    font-weight: bolder;
}

.my-skills-info-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 850px;
    max-height: 519px;
    z-index: 99;
    background-color: black;
    border-radius: 20px;
    margin: 15px auto;
    opacity: .93;
    padding: 20px 60px;
}

table {
    font-size: 14px;
    text-align: center;
    width: 100%;
    position: relative;
    top: 0;
    left: 0;
    max-width: 700px;
    margin: 15px auto 0;
    }
    th, td:first-child {
    background: transparent;
    color: white;
    padding: 10px;
    }
    th, td {
        border: none;
        background: transparent;
        margin: 0;
        width: 20%;
    }

    th:first-child, td:first-child {
        text-align: left;
    }

    @media screen and (max-width: 910px) {
        .my-skills-info-container {
            padding: 15px 45px;
        }
        
        .my-skills-info-container {
            opacity: .88;
        }
    }

    @media screen and (max-width: 800px) {
        table {
            font-size: 12px;
        }

        #my-skills-title {
            font-size: 28px;
        }

        .my-skills-info-container {
            border-radius: 20px;
            padding: 10px 30px;
        }
    }

    @media screen and (max-width: 680px) {
        table {
            font-size: 11px;
        }

        .my-skills-info-container {
            border-radius: 15px;
            padding: 8px 24px;
        }

        #my-skills-title {
            font-size: 26px;
        }
    }

    @media screen and (max-width: 630px) {
        table {
            font-size: 10px;
        }

        .my-skills-info-container {
            border-radius: 13px;
            padding: 6px 18px;
        }

        #my-skills-title {
            font-size: 24px;
        }

        th, td:first-child {
            padding: 8px;
        }
    }

    @media screen and (max-width: 550px) {
        table {
            font-size: 9px;
        }

        .my-skills-info-container {
            border-radius: 11px;
            padding: 6px 12px;
        }

        #my-skills-title {
            font-size: 22px;
        }

        th, td:first-child {
            padding: 7px;
        }
    }

    @media screen and (max-width: 500px) {
        table {
            font-size: 8px;
        }

        .my-skills-info-container {
            border-radius: 9px;
            padding: 6px 9px;
        }

        #my-skills-title {
            font-size: 20px;
        }

        th, td:first-child {
            padding: 6px;
        }
    }

    @media screen and (max-width: 435px) {
        table {
            font-size: 7px;
        }

        .my-skills-info-container {
            border-radius: 7px;
            padding: 6px;
        }

        #my-skills-title {
            font-size: 18px;
        }

        th, td:first-child {
            padding: 5px;
        }
    }

    @media screen and (max-width: 380px) {
        table {
            font-size: 6px;
        }

        .my-skills-info-container {
            border-radius: 5px;
        }

        #my-skills-title {
            font-size: 16px;
        }

        th, td:first-child {
            padding: 4px;
        }
    }

    @media screen and (max-width: 330px) {
        table {
            font-size: 5px;
        }

        .my-skills-info-container {
            border-radius: 4px;
        }

        #my-skills-title {
            font-size: 14px;
        }

        th, td:first-child {
            padding: 3px;
        }
    }

    @media screen and (max-width: 280px) {
        table {
            font-size: 4px;
        }

        .my-skills-info-container {
            border-radius: 2px;
        }

        #my-skills-title {
            font-size: 12px;
        }

        th, td:first-child {
            padding: 2px;
        }
    }

    @media screen and (max-height: 650px) {
        .my-skills-main-container {
            margin-bottom: 50px;
        }
    }

    @media screen and (max-height: 550px) {
        .my-skills-main-container {
