            margin-bottom: 150px;
        }
    }

    @media screen and (max-height: 350px) {
        .my-skills-main-container {
            margin-bottom: 200px;
            margin-top: 60px;
        }
    }

    @media screen and (max-height: 210px) {
        .my-skills-main-container {
            margin-bottom: 250px;
            margin-top: 100px;
        }
    }