.about-me-container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    font-family: 'Montserrat', sans-serif;
    color: white;
    padding: 100px 0;
    z-index: 9;
    background-color: #375387;
}

.about-me-title-container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 1300px;
    margin: 0 auto;
}

#about-me-title {
    font-size: 38px;
}

.about-me-description {
    font-size: 20px;
    display: inline-block;
    width: 70%;
    margin: 10px 0 0 0;
}


@media screen and (max-width: 900px) {
    .about-me-container {
        padding: 90px 0;
    }
  }

@media screen and (max-width: 768px) {
    .about-me-container {
        padding: 75px 0;
    }

    #about-me-title {
        font-size: 32px;
    }
  
    .about-me-description {
        font-size: 18px;
    }
  }

  @media screen and (max-width: 600px) {
    .about-me-container {
        padding: 50px 0;
    }

    #about-me-title {
        font-size: 28px;
    }
  
    .about-me-description {
        font-size: 16px;
    }
  }

  @media screen and (max-width: 445px) {
    .about-me-container {
        padding: 35px 0;
    }

    #about-me-title {
        font-size: 24px;
    }
  
    .about-me-description {
        font-size: 14px;
    }
  }

  @media screen and (max-width: 300px) {
    .about-me-container {
        padding: 20px 0;
    }

    #about-me-title {
        font-size: 18px;
    }
  
    .about-me-description {
        font-size: 12px;
    }
  }

