@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap');
/* #375387 #6991db */
html, body {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  text-align: center;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  overflow-y: hidden;
}

button {
  cursor: pointer;
}

.App {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  touch-action: pan-x pan-y;
  background: #375387;
}

h1, h2, h3, h4, h5, h6, p, ul, li {
  padding: 0;
  margin: 0;
}

/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #375387 #22375f;
}

/* Chrome, Edge and Safari */
*::-webkit-scrollbar {
  width: 11px;
  width: 11px;
}
*::-webkit-scrollbar-track {
  border-radius: 0px;
  background-color: #22375f;
}

*::-webkit-scrollbar-track:hover {
  background-color: #324870;
}

*::-webkit-scrollbar-track:active {
  background-color: #375387;
}

*::-webkit-scrollbar-thumb {
  border-radius: 0px;
  background-color: #6081c4;
}

*::-webkit-scrollbar-thumb:hover {
  background-color: #4967a1;
}

*::-webkit-scrollbar-thumb:active {
  background-color: #263c66;
}





